.title{
    color: #fff;
}

.reserves{
    padding: 20px;
    margin-bottom: 10px;
    background: #fff;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.reserves img{
    max-height: 100px;
}

.reserves button{
    border: 0;
    background: #fff;
}

.reserves svg{
    margin: 0 10px;
}

.reserves svg:hover{
    cursor: pointer;
}

footer button{
    border: 0;
    display: flex;
    padding: 10px;
    text-transform: uppercase;
    border-radius: 4px;
    margin-top: 10px;
    font-weight: bold;
}